/** grid system **/
.grid {
  display: flex;
  flex-wrap: wrap;
}

.grid_prices {
  margin-left: -20px;
  margin-right: -20px;
}

.grid_prices .grid__cell {
  display: flex;
  flex-direction: column;
  padding-left: 20px;
  padding-right: 20px;
  width: 25%;
}

.grid_prices .price {
  flex: 1;
}

@media (min-width: 992px) and (max-width: 1199px) {
  .grid_prices {
    margin-left: -15px;
    margin-right: -15px;
  }
  .grid_prices .grid__cell {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media (max-width: 991px) {
  .grid_prices .grid__cell {
    padding-left: 15px;
    padding-right: 15px;
    width: 50%;
  }
  .grid_prices .grid__cell:nth-child(odd) {
    align-items: flex-end;
  }
  .grid_prices .grid__cell:nth-child(n+3) {
    margin-top: 30px;
  }
  .grid_prices .price {
    width: 280px;
  }
}

@media (max-width: 767px) {
  .grid_prices .price {
    width: 248px;
  }
}

@media (max-width: 576px) {
  .grid_prices {
    margin-left: 0;
    margin-right: 0;
  }
  .grid_prices .price {
    width: 320px;
  }
  .grid_prices .grid__cell {
    padding-left: 0;
    padding-right: 0;
    width: 100%;
    align-items: center;
  }
  .grid_prices .grid__cell:nth-child(odd) {
    align-items: center;
  }
  .grid_prices .grid__cell:nth-child(n+2) {
    margin-top: 40px;
  }
}

@media (max-width: 420px) {
  .grid_prices .price {
    width: 100%;
  }
}

.grid_advantages {
  max-width: 876px;
  margin-left: auto;
  margin-right: auto;
}

.grid_advantages .grid__cell {
  padding-left: 20px;
  padding-right: 20px;
