  left: 0;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  white-space: nowrap;
  text-align: center;
  font-size: 0;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 6500;
  visibility: hidden;
  backface-visibility: hidden;
  opacity: 0;
  transform: scale(0);
  pointer-events: none;
  transition: visibility 0s ease 0.3s, transform 0s ease 0.3s, opacity 0.3s ease, width 0s ease 0.3s, height 0s ease 0.3s;
  /* opened popup */
}

.popup:before {
  height: 100%;
  display: inline-block;
  vertical-align: middle;
  content: '';
}

.popup__box {
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  white-space: normal;
  vertical-align: middle;
  text-align: left;
  padding: 15px 20px;
  width: 455px;
  font-size: 14px;
  opacity: 0;
  transform: scale(0.6);
  transition: transform .25s ease, opacity .25s ease;
}

.popup__body {
  position: relative;
  box-sizing: border-box;
  padding: 21px 29px 29px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.33);
  transition: padding .2s ease;
}

.popup__title {
  font-weight: bold;
  font-size: 30px;
  line-height: 34px;
  margin-bottom: 19px;
  box-sizing: border-box;
  padding-right: 20px;
}

.popup__close {
  position: absolute;
  top: 16px;
  right: 16px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  background-color: #fff;
}

.popup__close:hover .popup__close-icon {
  fill: #2d2e30;
}

.popup__close-icon {
  fill: #000;
  transition: fill 0.3s ease;
}

.popup_show {
  opacity: 1;
  visibility: visible;
  transform: scale(1);
  pointer-events: auto;
  transition: transform 0s ease, opacity 0.3s ease;
}

.popup_show .popup__box {
  opacity: 1;
  transform: scale(1);
}

@media screen and (max-width: 576px) {
  .popup__box {
    width: 100%;
  }
  .popup__title {
    font-size: 24px;
    line-height: 28px;
  }
  .popup__close {
    top: 8px;
    right: 8px;
  }
}

@media screen and (max-width: 576px) {
  .popup__body {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (max-width: 360px) {
  .popup__body {
    padding-left: 15px;
    padding-right: 15px;
  }
}

