  justify-content: center;
  align-items: flex-end;
}

.total__box .price__tarif {
  margin-bottom: 0;
}

.total__title {
  margin-right: 10px;
  font-size: 40px;
  line-height: 50px;
}

.total__buttons {
  margin-top: 15px;
}
